.sidebar {
	width: 300px;
}

.sidebar ul {
	flex-flow: column nowrap;
}

.sidebar li {
	float: left;
	list-style: none;
	padding: 5px;
	padding-top: 8px;
	font-family: "futuraStdHeavy";
	font-size: 1vw;
	color: #e15829;
	border-bottom: 1px solid black;
	width: 100%;
}

.sidebar i {
	float: right;
}

.sidebar button {
	margin-top: 5em;
	padding: 1vh;
	float: inline-start;
	background-color: transparent;
	color: #e15829;
	font-family: "futuraStdHeavy";
	font-size: 1.2vw;
	width: 100%;
	border-radius: 1vh;
}

.sidebar li:hover:not(.active) {
	background-color: #e15829;
	color: white;
}

button:hover {
	background-color: #e15829;
	color: white;
}

button:active {
	background-color: #e15829;
	color: white;
}

li a:active {
	background-color: #e15829;
	color: white;
}

@media screen and (max-width: 414px) {
	.sidebar button {
		font-size: 10px;
	}
	.sidebar li {
		float: left;
		list-style: none;
		padding: 5px;
		padding-top: 8px;
		font-family: "futuraStdHeavy";
		font-size: 10px;
		color: #e15829;
		border-bottom: 1px solid black;
		width: 100%;
	}
}
