.service-head {
	width: 100%;
	position: relative;
	text-align: center;
	color: white;
	background: #404041;
}

.property-management-background img {
	width: 10%;
	position: relative;
	top: 0;
	left: 0;
	z-index: 1;
	background-color: white;
}

.logoWE {
	position: absolute;
	top: 150px;
	left: 100px;
	z-index: 2;
}

.property-management-head-text-top {
	position: absolute;
	text-align: right;
	padding-right: 50px;
	top: 95%;
	left: 35%;
	transform: translate(3%, -50%);
	font-family: futuraStdBook;
	font-size: 4vw;
}

.property-management-head-text-bottom {
	position: absolute;
	bottom: -5%;
	right: 24%;
	transform: translate(50%, 50%);
	font-family: futuraStdBook;
	font-size: 4vw;
	color: #e15829;
}
